import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Bot, Settings, ChevronRight, CheckCircle, XCircle, AlertCircle } from "lucide-react";
import { useState, useEffect } from "react";
import { Skeleton } from "@/components/ui/skeleton";

interface ModelConfig {
  name: string;
  baseURL: string;
}

const SUPABASE_URL = import.meta.env.VITE_SUPABASE_URL;
const SUPABASE_KEY = import.meta.env.VITE_SUPABASE_PUBLISHABLE_KEY;

interface AIProviderConfig {
  id: string;
  name: string;
  description: string;
  icon: React.ElementType;
  iconColor: string;
  configurable: boolean;
  configPath?: string;
  status?: 'configured' | 'not-configured' | 'no-config-needed';
  modelName?: string;
}

const AdminAIConfigList = () => {
  const { t } = useTranslation();
  const [agentModelConfig, setAgentModelConfig] = useState<ModelConfig | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  // Fetch agent model config to show status
  useEffect(() => {
    const fetchAgentConfig = async () => {
      try {
        const response = await fetch(
          `${SUPABASE_URL}/functions/v1/xiaoxiong-agent-model?type=main&endpoint=model`,
          {
            method: 'GET',
            headers: {
              'Authorization': `Bearer ${SUPABASE_KEY}`,
              'Content-Type': 'application/json',
            },
          }
        );

        if (response.ok) {
          const result = await response.json();
          if (result?.success && result?.data) {
            setAgentModelConfig(result.data);
          }
        }
      } catch (error) {
        console.error("Failed to fetch agent config:", error);
      } finally {
        setIsLoading(false);
      }
    };

    fetchAgentConfig();
  }, []);

  const aiProviders: AIProviderConfig[] = [
    {
      id: 'ask-xiaoxiong',
      name: t("admin.aiSettings.providers.askXiaoxiong", "小熊AI"),
      description: t("admin.aiSettings.providers.askXiaoxiongDesc", "原有AI接口，直接问答"),
      icon: Bot,
      iconColor: "text-primary",
      configurable: false,
      status: 'no-config-needed',
    },
    {
      id: 'xiaoxiong-agent',
      name: t("admin.aiSettings.providers.xiaoxiongAgent", "小熊Agent"),
      description: t("admin.aiSettings.providers.xiaoxiongAgentDesc", "新AI接口，支持模型配置"),
      icon: Settings,
      iconColor: "text-orange-500",
      configurable: true,
      configPath: '/admin/ai-config/xiaoxiong-agent',
      status: agentModelConfig?.name ? 'configured' : 'not-configured',
      modelName: agentModelConfig?.name,
    },
  ];

  const getStatusBadge = (provider: AIProviderConfig) => {
    if (provider.status === 'no-config-needed') {
      return (
        <Badge variant="secondary" className="text-xs">
          <CheckCircle className="h-3 w-3 mr-1" />
          无需配置
        </Badge>
      );
    }
    if (provider.status === 'configured') {
      return (
        <Badge variant="default" className="text-xs bg-green-500">
          <CheckCircle className="h-3 w-3 mr-1" />
          已配置
        </Badge>
      );
    }
    return (
      <Badge variant="outline" className="text-xs text-orange-500 border-orange-500">
        <AlertCircle className="h-3 w-3 mr-1" />
        待配置
      </Badge>
    );
  };

  return (
    <div className="p-6 space-y-6 max-w-4xl mx-auto">
      {/* Header */}
      <div className="flex items-center gap-3">
        <Settings className="h-8 w-8 text-primary" />
        <div>
          <h1 className="text-2xl font-bold">{t("admin.aiConfig.title", "AI配置管理")}</h1>
          <p className="text-muted-foreground">
            {t("admin.aiConfig.subtitle", "管理和配置各个AI服务的模型和参数")}
          </p>
        </div>
      </div>

      {/* AI Providers List */}
      <div className="grid grid-cols-1 gap-4">
        {isLoading ? (
          <>
            <Skeleton className="h-32 w-full" />
            <Skeleton className="h-32 w-full" />
          </>
        ) : (
          aiProviders.map((provider) => (
            <Card 
              key={provider.id} 
              className={`transition-all ${provider.configurable ? 'hover:border-primary/50 cursor-pointer' : ''}`}
            >
              <CardHeader className="pb-3">
                <div className="flex items-start justify-between">
                  <div className="flex items-center gap-3">
                    <div className={`p-2 rounded-lg bg-muted ${provider.iconColor}`}>
                      <provider.icon className="h-6 w-6" />
                    </div>
                    <div>
                      <CardTitle className="text-lg flex items-center gap-2">
                        {provider.name}
                        {getStatusBadge(provider)}
                      </CardTitle>
                      <CardDescription className="mt-1">
                        {provider.description}
                      </CardDescription>
                    </div>
                  </div>
                  
                  {provider.configurable && provider.configPath && (
                    <Button variant="outline" size="sm" asChild>
                      <Link to={provider.configPath}>
                        配置
                        <ChevronRight className="h-4 w-4 ml-1" />
                      </Link>
                    </Button>
                  )}
                </div>
              </CardHeader>
              
              {provider.configurable && (
                <CardContent className="pt-0">
                  <div className="bg-muted/30 rounded-lg p-3 text-sm">
                    {provider.modelName ? (
                      <div className="flex items-center gap-2">
                        <span className="text-muted-foreground">当前模型:</span>
                        <code className="px-2 py-0.5 bg-background rounded text-xs font-mono">
                          {provider.modelName}
                        </code>
                      </div>
                    ) : (
                      <p className="text-muted-foreground">
                        尚未配置模型，点击右侧"配置"按钮进行设置
                      </p>
                    )}
                  </div>
                </CardContent>
              )}
            </Card>
          ))
        )}
      </div>

      {/* Quick Links */}
      <Card className="bg-muted/30">
        <CardContent className="pt-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="font-medium">需要更改主页使用的AI接口？</p>
              <p className="text-sm text-muted-foreground">
                前往AI管理页面切换主页使用的AI服务提供商
              </p>
            </div>
            <Button variant="outline" size="sm" asChild>
              <Link to="/admin/ai-settings">
                前往AI管理
                <ChevronRight className="h-4 w-4 ml-1" />
              </Link>
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default AdminAIConfigList;
